html,
body {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  flex-direction: column;
}


.scoreboard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card {
  border: 1px solid #0000ff;
  display: flex;

  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.container {
  height: 1000px;
  padding: 40px;
  display: grid;
  grid-gap: 30px;
  gap: 30px;
  grid-template-columns: repeat(3, 230px);
  justify-content: center;
  border: 1px solid black;
}

img {
  min-width: 100%;
  min-height: 100%;
}

